
.error p {
  font-size: 50px;
  letter-spacing: 0.3em;
  color: #f66;
}
.error p:nth-of-type(2) {
  margin-top: -0.5em;
}
.error ul {
  list-style: none;
  padding-left: 0;
}
.error ul li {
  font-size: 26px;
}
.error ul li:nth-of-type(1) {
  color: #66beff;
}
.error ul li:nth-of-type(2) {
  margin-top: 10px;
  color: #13ce66;
}


